html {
  scroll-behavior: smooth;
}

#scroll-up {
  height: 70px;
  width: 70px;
  cursor: pointer;
  border-radius: 50%;
  align-items: center;
  justify-items: center;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

#scroll-up-value {
  height: calc(100% - 15px);
  width: calc(100% - 15px);
  color: #001a2e;
  background-color: #fff;
  border-radius: 50%;
  align-items: center;
  justify-items: center;
  font-size: 35px;
  display: grid;
}

body.show-modal .backdrop {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: rgba(17, 17, 17, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.modal {
